
.panel {
  border: 1px solid #488aff;
  border-radius: 4px;
}
.panel .panel-header {
    padding: 0.5rem 0 0.5rem 1rem;
    text-align: left;
    border-bottom: 1px solid #488aff;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    color: #fff;
    background-color: #488aff;
}
.panel .panel-header h2 {
      margin: 0;
      font-size: 1.5rem;
}
.panel .panel-body {
    padding: 1rem;
}



