
.drawlayout[data-v-e9e75fca] {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: all 0.5s ease;
}
.drawlayout .shade[data-v-e9e75fca] {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(20, 20, 20, 0.5);
}
.drawlayout .content-section[data-v-e9e75fca] {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  background-color: #fff;
}
.drawlayout.drawlayout-enter .shade[data-v-e9e75fca],
.drawlayout.drawlayout-leave-to .shade[data-v-e9e75fca] {
  opacity: 0;
}
.drawlayout.drawlayout-enter-active .shade[data-v-e9e75fca],
.drawlayout.drawlayout-leave-active .shade[data-v-e9e75fca] {
  transition: opacity 0.5s;
}
.drawlayout.drawlayout-enter .content-section[data-v-e9e75fca],
.drawlayout.drawlayout-leave-to .content-section[data-v-e9e75fca] {
  width: 0 !important;
}
.drawlayout.drawlayout-enter-active .content-section[data-v-e9e75fca],
.drawlayout.drawlayout-leave-active .content-section[data-v-e9e75fca] {
  transition: width 0.5s;
}



